<template>
  <div id="animateBox">
    <div class="pageBox">
        <keep-alive>
          <router-view :key="key"></router-view>
        </keep-alive>  
    </div>
    <ul class="tab">
      <router-link to="home" class="borderT" tag='li' replace></router-link>
      <router-link to="classify" class="borderT" tag='li' replace></router-link>
      <router-link to="order" class="borderT" tag='li' replace></router-link>
      <router-link to="my" class="borderT" tag='li' replace></router-link>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'index',
  mounted: function() {
  },
  computed: {
    key() {
        return this.$route.path.replace(/\//g, '_')
    }
  },
  methods:{
  }
}  
</script>
<style scoped>
.pageBox{ position:absolute; bottom: 0; width: 100%; top: 0; padding-bottom: 90px; overflow-y: scroll; -webkit-overflow-scrolling: touch; }
.tab{ text-align: center; width: 100%; height: 90px; line-height: 90px; font-size: 28px; position: absolute; bottom: 0; left: 0; }
.tab li{ width: 25%; float: left; line-height: 90px; background-color: #fff}
.tab li a{ display: block; width: 100%; height: 100%; }
</style>


