<template>
<div id="home">	
	<header class="publicHead yellow">
		<div class="homeHead">
			<div class="address">
				<p>198</p>
			</div>
			<div class="button">
				
			</div>
		</div>
	</header>
	<div class="pageView">
<!-- 	<div>home</div>
	<ul style="height:30px; width:100%;">
	    <li><router-link to="pageA">index</router-link></li>
	    
	</ul> -->
		<div class="swiper-container top">
		    <div class="swiper-wrapper">
		        <div v-for="(value, key, index) in imgurls" class="swiper-slide">
					<img width="100%" :src="value.imgUrl" alt="">
		        </div>
		    </div>
		    <div class="swiper-pagination"></div>
		</div>
		<ul class="nav">
			<router-link to="nav"><li><img src="../static/img/WechatIMG195_03.png" alt=""><p></p></li></router-link>
			<router-link to="nav"><li><img src="../static/img/WechatIMG195_05.png" alt=""><p></p></li></router-link>
			<router-link to="nav"><li><img src="../static/img/WechatIMG195_07.png" alt=""><p></p></li></router-link>
			<router-link to="nav"><li><img src="../static/img/WechatIMG195_09.png" alt=""><p></p></li></router-link>
		</ul>
		<ul class="active">
			<li v-for="(value, key,index) in acImgurls">
				<p><img width="100%" :src="value.imgUrl" alt=""></p>
				<div class="swiper-container nerb">
			        <ul class="swiper-wrapper">
						<router-link v-for="(value,key,index) in value.itemList
" class="swiper-slide borderR" :to="{path:'detail',query: {itemId:value.itemId}}" tag='li' :key="index">
							<img width="100%" :src="value.imgUrl" alt="">
				            <p><span class="fr">{{value.itemBrand}}</span><span class="fl_name">{{value.itemName}}</span></p>
			            </router-link>
			        </ul>
			    </div>
			</li>
		</ul>	
	</div>
</div>
</template>

<script>
import '../static/css/swiper.min.css'
import '../static/js/swiper.min.js'
export default {
  name: 'home',
  data () {
    return {
      imgurls: {},
      acImgurls:{}
    }
  },
  mounted: function() {     
    this.getCustomers();
    this.getList();
  },
  methods:{
  	banner:function(){
  		var swiper = new Swiper('.swiper-container', {
	        pagination: '.swiper-pagination',
	        paginationClickable: true
	    });
  	},
  	activeList:function(){
		var swiper2 = new Swiper('.nerb', {
	        slidesPerView: 'auto',
	        paginationClickable: true,
	        spaceBetween: 0,
	        freeMode: true
		});
  	},
    getCustomers: function() {
      var vm = this
      vm.$http.jsonp('https://www.52shangou.com/api/shop.do?ak=aMdi6T4a2kA%3D&dt=1&lid=1211549&md=1&pc=zx_1&pos=&sn=4100d21a1bb67d8815a34efb03ebd349&tsid=8ED8028E-D05C-4129-9780-6B5C2C971076')
        .then((response) => {
        	vm.imgurls=response.data.extra.adv.oneHourBanner;
			// console.log(response.data.extra.adv.oneHourBanner[0].imgUrl);
			console.log(response.data);
			// this.msg=response.data.extra.cityname;
			vm.$nextTick(vm.banner);
        })
    },
    getList: function(){
      var vm = this
      vm.$http.jsonp('https://www.52shangou.com/api/locate.do?ak=aMdi6T4a2kA%3D&dt=1&lat=30.327653&lng=120.172275&md=1&nlid=0&pc=zx_2&sn=9bcb7e7935e0d723dc982faff2e61743&tsid=E020420E-55D7-4500-8E73-C0E32101507Cs')
        .then((response) => {
			vm.acImgurls=response.data.data.adv.boutiqueCategory;
        	console.log(response.data);
        	vm.$nextTick(vm.activeList);
        })
    }
  }
}
</script>
<style scoped>
	.swiper-container.nerb {width: 1;background: #fff;height: auto;}
	.swiper-container.nerb  .swiper-slide {text-align: center;width: auto;background-color: #fff;-margin-left: 0.3rem;-margin-top: 0.4rem; width: 220px; height: 100%; -border:1px solid #ccc; box-sizing: border-box; float: left; position: relative; -border-bottom:none; }
		

 	.swiper-container.top {
        width: 100%;
        height: auto;
        min-height: 240px;
        margin: 0 auto;
    }
    .swiper-container.top .swiper-slide {
        text-align: center;
        background: #fff;
        /* Center slide text vertically */
        display: -webkit-box;
        display: -ms-flexbox;
        display: -webkit-flex;
        display: flex;
        -webkit-box-pack: center;
        -ms-flex-pack: center;
        -webkit-justify-content: center;
        justify-content: center;
        -webkit-box-align: center;
        -ms-flex-align: center;
        -webkit-align-items: center;
        align-items: center;
    }
    #home{ background: #f6f6f6; }
	#home .address{ float: left; font-size: 26px;}
	#home .button{ float: right; }
	#home .homeHead{ padding: 0 40px;}
	.nav{ width: 100%; padding: 0.4rem 0 0.2rem 0; overflow: hidden; background: #fff;}
	.nav li{ width: 25%; text-align: center; float: left; }
	.nav li p{ font-size: 18px; }
	.nav li img{ width: 110px; }
	.active li p{ width: 100%; height: auto; background: #fff; }
</style>


