			        <div class="gray f24">
			          <span>{{detailData.property}}</span>
			          <span></span>
			          <span>{{detailData.unit ? '/'+detailData.unit :''}}</span>
			          <span class="givedesc"></span>
			        </div>
			        <div class="textArea">
			          <div class="textAreaL gray">
			            <span class="f30 red"></span>
			            <span class="red price">{{(detailData.offlinePrice*0.01).toFixed(2)}}</span>
			            <s></s>
			          </div>
			          <div class="textAreaR f24 gray">
			            <div class="clicks">
			              <span class="clicksIcon"></span>
			              <span>{{detailData.browseQuantity}}</span></div>
			          </div>
			        </div>
			      </div>
			    </div>
			    <div></div>
			    <div>
			      <div class="shopEntrance">
			        <div class="shop-photo">
			          <span class="shopIcon" :style="{backgroundImage: 'url(' + detailData.shopFavicon + ')'}"></span>
			          <span class="shop-status off"></span></div>
			        <div>
			          <span class="shopName">
			            <span>{{detailData.shopName}}</span></span>
			          <span class="hours">
