			        <div class="more"></div>
			      </div>
			    </div>
			    <div ></div>
			    <div >
			      <div class="hotTitle" ></div>
			      <div class="hotList" >
			        <div v-for="(value, key,index) in list" class="suggestItem" >
			        <router-link :to="{path:'detail',query: {itemId:value.id}}" tag='div'>
			          <div class="fullReduce" >
			            <div class="fullReduceBg" ></div>
			            <div class="fullReduceName" ></div></div>
			          <div class="itemListCellItem" >
