<script>
export default {
  name: 'listdetail',
  data () {
    return {
      imgPath:'http://imgsize.52shangou.com/img/',
      itemId:this.$route.query.itemId,
      detailData: {},
      list:{}
    }
  },
  ready:function(){
  	window.scrollTo(0, 0);
  },
  created: function() {
    this.getData(this.itemId);
  },
  methods: {
  	// go(){
  	// 	this.$router.push({path:"detail",query:{itemId:"101989470"}})
  	// },
    goback () {
      this.$router.goBack()
    },
    getData(id){
	  let vm = this;
      vm.$http.jsonp('https://www.52shangou.com/itemcenter/buyer/open/item/detail.jsonp?itemId='+id+'')
        .then((response) => {
        	vm.detailData=response.data.entry;
			console.log(response.data);
			vm.getList();
        })
    },
    getList(){
      let vm = this;
      vm.$http.jsonp('https://www.52shangou.com/itemcenter/buyer/open/item/detail/relations.jsonp?catId='+vm.detailData.catId+'&shopId='+vm.detailData.shopId+'&shopIds=&itemId=104042711')
        .then((response) => {
        	vm.list=response.data.entry.items;

			console.log(response.data.entry.items);
        })
    }
  },
  activated (){
  	this.itemId=this.$route.query.itemId;
    this.getData(this.itemId);
  }
}
</script>
